@charset "UTF-8";
/* Fade In Top */
.fade-in-top {
  animation: fadeInTop 1s ease-out forwards;
}

@keyframes fadeInTop {
  from {
    opacity: 0;
    transform: translateY(-300px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/* Fade In Bottom */
.fade-in-bottom {
  animation: fadeInBottom 1s ease-out forwards;
}

@keyframes fadeInBottom {
  from {
    opacity: 0;
    transform: translateY(300px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/* Fade In Left */
.fade-in-left {
  animation: fadeInLeft 1s ease-out forwards;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-300px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
/* Fade In Right */
.fade-in-right {
  animation: fadeInRight 1s ease-out forwards;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(300px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
:root {
  --primary-yellow: #C2A242;
  --primary-yellow-light: #FFFFCC;
  --light-yellow: #FAE78D;
  --light-yelloww: #FFF9C4;
  --dark-yellow: #D4B72E;
  --very-light-yellow: #FEF9E7;
  --medium-yellow: #E5C349;
  --golden-yellow: #B89720;
  --color-primary: #030305; /* Preto Profundo, usado para texto e elementos chave */
  --color-secondary: #3C3428; /* Marrom Escuro, bom para cabeçalhos ou barras de navegação */
  --color-tertiary: #7C6947; /* Marrom Médio, ideal para rodapés e subtítulos */
  --color-highlight: #CBA75C; /* Dourado, perfeito para botões e links */
  --color-background: #E5E5E5; /* Quase Branco, para fundos e áreas de conteúdo */
  /* Adicionalmente, você pode querer ter uma cor de fundo mais escura para contrastar */
  --color-dark-background: #252525; /* Cinza Escuro, para fundos alternativos ou seções em destaque */
  --teste-1: #FFE47A;
  --teste-2: #FFEDA5;
  --teste-3: #E1BD2E;
  --teste-4: #B49414;
  --teste-5: #E1BD2E;
  --teste-6: #B49414;
  --teste-7: #FFE47A;
  --light-gray: #F0F0F0;
  --medium-gray: #B0B0B0;
  --dark-gray: #333333;
  --light-green: #C5E1A5;
  --dark-green: #388E3C;
  --lime-green: #DFF3E3;
  --forest-green: #2C6E49;
  --light-blue: #A7C7E7;
  --dark-blue: #2A4D69;
  --primary-blue: #007A78;
  --black-light: #1A1A1A;
  --black-lighten: #192033;
  --black-normal: #010101;
  --black-dark: #000000;
  --white: #FFFFFF;
  --white-normal: #F5F5F5;
  --white-dark: #D9D9D9;
  --gray-900: #030E15;
  --gray-800: #2D3235;
  --gray-700: #4F5152;
  --gray-600: #78797A;
  --gray-500: #9A9D9E;
  --gray-400: #BABCBD;
  --gray-300: #C6C7C8;
  --gray-200: #DADCDD;
  --gray-100: #F6F6F6;
